import React from "react";
import Divisions from "./Divisions";
import Netwins from "./Netwins";
import Back from "./Back";
import Content from "./Content";
import DivisionsCalc from "./DivisionsCalc";
import NetwinsCalc from "./NetwinsCalc";

class MainContent extends React.Component{
    constructor(){
        super();
        this.state = {
            method: -1,
            divisionsId: 0,
            netwinsId: 1
        };
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(id){
        this.setState({method: id});
    }

    render(){
        return(
            <main>
                <Back handleClick={this.handleClick} appear = {this.state.method != -1} />
                <div className="main-content">
                    <Content id = {this.state.divisionsId}
                             selected={this.state.method == this.state.divisionsId} 
                             name="Divisions" 
                             handleClick={this.handleClick} 
                             appear={this.state.method != this.state.netwinsId}>
                        <DivisionsCalc />
                    </Content>
                    <Content id = {this.state.netwinsId}
                             selected={this.state.method == this.state.netwinsId} 
                             name="Netwins" 
                             handleClick={this.handleClick} 
                             appear={this.state.method != this.state.divisionsId}>
                        
                        <NetwinsCalc />
                    </Content>
                </div>
            </main>
        );
    }

}

export default MainContent;