import React from "react"
import prices from "../prices.json"

class DivisionsCalc extends React.Component{
    constructor(){
        super();
        this.state = {
            price: 0,
            startingRank: "iron",
            startingDivision: "4",
            endingRank: "iron",
            endingDivision: "4",
            soloDuo: "solo"
        };

        this.handleChange = this.handleChange.bind(this);
        this.calculatePrice = this.calculatePrice.bind(this);
        this.rankToID = this.rankToID.bind(this);
        this.idToRank = this.idToRank.bind(this);
    }

    rankToID(rank){
        switch (rank) {
            case "iron":
                return 0;
            case "bronze":
                return 1;
            case "silver":
                return 2;
            case "gold":
                return 3;
            case "platinum":
                return 4;
            case "diamond":
                return 5;
        }
    }

    idToRank(id){
        switch(id){
            case 0:
                return "iron";
            case 1:
                return "bronze";
            case 2:
                return "silver";
            case 3:
                return "gold";
            case 4:
                return "platinum";
            case 5:
                return "diamond";
        }
    }

    calculatePrice(soloDuo, startingRank, startingDivision, endingRank, endingDivision){
        let total = 0;
        let rank = startingRank;
        let division = startingDivision;

        if(startingRank <= endingRank){
            while(rank <= endingRank || division != endingDivision){
                for(; division > (rank == endingRank ? endingDivision : 0); division--){
                    total += prices.division[soloDuo][this.idToRank(rank)][4 - division];
                }
                if(rank == endingRank){
                    break;
                }

                if(rank <= endingRank || division != endingDivision){
                    division = 4;
                    rank++;
                }
            }

            return total;
        }
        return -1;
    }

    handleDropDownMouseOver(){

    }

    handleChange(event){
        const {name, value} = event.target;
        const soloDuo = name === "soloDuo" ? value : this.state.soloDuo;
        const startingRank = this.rankToID(name === "startingRank" ? value : this.state.startingRank);
        const startingDivision = name === "startingDivision" ? value : this.state.startingDivision;
        const endingRank = this.rankToID(name === "endingRank" ? value : this.state.endingRank);
        const endingDivision = name === "endingDivision" ? value : this.state.endingDivision;

        const newPrice = this.calculatePrice(soloDuo, startingRank, startingDivision, endingRank, endingDivision);

        console.log(newPrice);
        this.setState({
            [name]: value,
            price: newPrice
        })

    }

    render(){
        return(
            <div>
                <form>
                    <input 
                        type="radio"
                        name="soloDuo"
                        value="solo"
                        checked={this.state.soloDuo === "solo"}
                        onChange={this.handleChange}>
                    </input><span style={{fontSize: "1.2rem"}}>Solo</span>
                    <input 
                        type="radio"
                        name="soloDuo"
                        value="duo"
                        checked={this.state.soloDuo === "duo"}
                        onChange={this.handleChange}>
                    </input><span style={{fontSize: "1.2rem"}}>Duo</span>
                    <br />
                    <select
                        value={this.state.startingRank}
                        onChange={this.handleChange}
                        name="startingRank">

                        <option value="iron">Iron</option>
                        <option value="bronze">Bronze</option>
                        <option value="silver">Silver</option>
                        <option value="gold">Gold</option>
                        <option value="platinum">Platinum</option>
                        <option value="diamond">Diamond</option>
                    </select>

                    <select
                        value={this.state.startingDivision}
                        onChange={this.handleChange}
                        name="startingDivision">

                        <option value="4">4</option>
                        <option value="3">3</option>
                        <option value="2">2</option>
                        <option value="1">1</option>
                    </select>

                    <span style={{ fontSize: "1rem", margin: 5 }}>to</span>

                    <select
                        value={this.state.endingRank}
                        onChange={this.handleChange}
                        name="endingRank">

                        <option value="iron">Iron</option>
                        <option value="bronze">Bronze</option>
                        <option value="silver">Silver</option>
                        <option value="gold">Gold</option>
                        <option value="platinum">Platinum</option>
                        <option value="diamond">Diamond</option>
                    </select>

                    <select
                        value={this.state.endingDivision}
                        onChange={this.handleChange}
                        name="endingDivision">

                        <option value="4">4</option>
                        <option value="3">3</option>
                        <option value="2">2</option>
                        <option value="1">1</option>
                    </select>
                </form> 
                <p>${this.state.price}</p>
            </div>
        );
    }

}

export default DivisionsCalc