import React from "react";
import prices from "../prices.json";

class NetwinsCalc extends React.Component{
    constructor(){
        super();
        this.state = {
            price: 0,
            soloDuo: "solo",
            rank: "iron",
            division: "4",
            wins: 0
        };
        
        this.handleChange = this.handleChange.bind(this);
        this.calculatePrice = this.calculatePrice.bind(this);
    }

    calculatePrice(soloDuo, rank, division, wins){
        return wins * prices.netwins[soloDuo][rank][4 - division];
    }

    handleChange(event){
        const {name, value} = event.target;
        const soloDuo = name === "soloDuo" ? value : this.state.soloDuo;
        const rank = name === "rank" ? value : this.state.rank;
        const division = name === "division" ? value : this.state.division;
        const wins = name === "wins" ? value : this.state.wins;

        const newPrice = this.calculatePrice(soloDuo, rank, division, wins);

        this.setState({
            [name]: value,
            price: newPrice
        })
    }

    render(){
        return (
            <div>
                <form>
                    <input
                        type="radio"
                        name="soloDuo"
                        value="solo"
                        checked={this.state.soloDuo === "solo"}
                        onChange={this.handleChange}>
                    </input><span style={{ fontSize: "1.2rem" }}>Solo</span>
                    <input
                        type="radio"
                        name="soloDuo"
                        value="duo"
                        checked={this.state.soloDuo === "duo"}
                        onChange={this.handleChange}>
                    </input><span style={{ fontSize: "1.2rem" }}>Duo</span>
                    <br />
                    <select
                        value={this.state.rank}
                        onChange={this.handleChange}
                        name="rank">

                        <option value="iron">Iron</option>
                        <option value="bronze">Bronze</option>
                        <option value="silver">Silver</option>
                        <option value="gold">Gold</option>
                        <option value="platinum">Platinum</option>
                        <option value="diamond">Diamond</option>
                    </select>

                    <select
                        value={this.state.division}
                        onChange={this.handleChange}
                        name="division">

                        <option value="4">4</option>
                        <option value="3">3</option>
                        <option value="2">2</option>
                        <option value="1">1</option>
                    </select>
                    <br />
                    <span style={{ fontSize: "1.2rem" }}>Wins: </span>
                    <input
                        type="text"
                        name="wins"
                        value={this.state.wins}
                        onChange={this.handleChange}
                        style={{width: 20, textAlign: "center"}}/>
                </form>
                <p>${this.state.price}</p>
            </div>
        );
    }
}

export default NetwinsCalc;