import React from "react"

class Netwins extends React.Component{
    constructor(){
        super();
        this.state = {
            startingRank: "Iron",
            price: 0,
            filter: "",
            width: 220,
            height: 220,
            mouseOver: false
        };
    }

    render(){
        let style = {
            display:this.props.otherSelected ? "none" : "flex", 
            transition: this.props.selected ? "none" : "filter 300ms, width 300ms, height 300ms, font-size 300ms",
            filter: this.state.filter
        };

        this.state.mouseOver && (style.height = this.state.height);
        this.state.mouseOver && (style.width = this.state.width);

        return(
            <a href="#" style={style} className="content" onClick={event => this.props.handleClick(1)} onMouseExit={event => {this.setState({mouseOver:false})}} onMouseMove={event => {this.setState({mouseOver:true, filter: this.props.selected ? "brightness(1)" : "brightness(1.4)"})}}>
                {this.props.selected ? 
                <div>
                    <form>
                        <span style={{fontSize: "1rem", margin: 5}}>from</span>

                        <select
                            value={this.state.startingRank}
                            onChange={this.handleChange}
                            name="startingRank">
                            
                            <option value="iron">Iron</option>
                            <option value="bronze">Bronze</option>
                            <option value="silver">Silver</option>
                            <option value="gold">Gold</option>
                            <option value="platinum">Platinum</option>
                            <option value="diamond">Diamond</option>
                        </select>
                        
                        <select
                            value={this.state.startingRank}
                            onChange={this.handleChange}
                            name="startingDivision">
                            
                            <option value="4">4</option>
                            <option value="3">3</option>
                            <option value="2">2</option>
                            <option value="1">1</option>
                        </select>

                        <span style={{fontSize: "1rem", margin: 5}}>to</span>

                        <select
                            value={this.state.startingRank}
                            onChange={this.handleChange}
                            name="startingRank">
                            
                            <option value="iron">Iron</option>
                            <option value="bronze">Bronze</option>
                            <option value="silver">Silver</option>
                            <option value="gold">Gold</option>
                            <option value="platinum">Platinum</option>
                            <option value="diamond">Diamond</option>
                        </select>

                        <select
                            value={this.state.startingRank}
                            onChange={this.handleChange}
                            name="startingDivision">
                            
                            <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                        </select>

                        <br/>
                        <br/>
                        ${this.state.price}
                    </form>
                </div>
                : <p>Netwins</p>}
            </a>
        );
    }

}

export default Netwins;