import React from "react";
import { render } from "@testing-library/react";

class Content extends React.Component{
    constructor(){
        super();
        this.state = {
            width: 200,
            height: 200,
            filter: "brightness(1)"
        };
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseExit = this.handleMouseExit.bind(this);
        this.handleMouseMove = this.handleMouseMove.bind(this);
    }

    handleMouseMove(){
        if(this.props.selected){
            this.setState({
                width: 200,
                height: 200,
                filter: "brightness(1)"
            });
        }
    }

    handleMouseEnter(){
        if(!this.props.selected){
            this.setState({
                width: 220,
                height: 220,
                filter: "brightness(1.4)"
            });
        }
    } 
    
    handleMouseExit(){
        if(!this.props.selected){
            this.setState({
                width: 200,
                height: 200,
                filter: "brightness(1)"
            });
        }
    }

    render(){
        const style = {
            width: this.props.appear ? this.props.selected ? 400 : this.state.width : 0,
            height: this.props.appear ? this.props.selected ? 200 : this.state.height : 0,
            filter: this.props.selected ? "brightness(1)" : this.state.filter
        }

        const buttonStyle = {
            width: this.state.width,
            height: this.state.height
        }

        return (
            <div className="content" 
                 style={style} 
                 onMouseMove={this.handleMouseMove} 
                 onMouseEnter={this.handleMouseEnter} 
                 onMouseLeave={this.handleMouseExit}>
                {this.props.selected 
                ? 
                this.props.children
                : 
                this.props.appear && 
                <a href="#" 
                   className="content-button" 
                   style={buttonStyle} 
                   onClick={event => this.props.handleClick(this.props.id)}>
                    <p>{this.props.name}</p>
                </a>
                }
            </div>
        );
    }
}

export default Content;